import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import CustomThree from '../views/CustomThree.vue'
import CustomTwo from '../views/CustomTwo.vue'
import CustomOne from '../views/CustomOne.vue'
import CustomFour from'../views/CustomFour.vue'


const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/custom3',
    name: 'CustomThree',
    component: CustomThree
  },
  {
    path: '/custom2',
    name: 'CustomTwo',
    component: CustomTwo,
  },
  {
    path: '/custom1',
    name: 'CustomOne',
    component: CustomOne
  },
  {
    path: '/custom4',
    name: 'CustomFour',
    component: CustomFour
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
