import { createApp } from 'vue'
import App from './App.vue'
import router from './router'


/* import the fontawesome core */
import { library } from '@fortawesome/fontawesome-svg-core'

/* import font awesome icon component */
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

/* import specific icons */
import { faBrush, faEnvelope, faLocationDot, faPhone, faUserSecret, faWarehouse, faXmark, } from '@fortawesome/free-solid-svg-icons'
import { faBars } from '@fortawesome/free-solid-svg-icons'
import { faBriefcase } from '@fortawesome/free-solid-svg-icons'

/* add icons to the library */
library.add(faUserSecret, faBars, faBriefcase, faPhone, faXmark, faBrush, faEnvelope, faWarehouse, faLocationDot, faPhone)


createApp(App).use(router).component('font-awesome-icon', FontAwesomeIcon).mount('#app')

